import React from "react"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../components/Layout"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import "../font-awesome"
import { Container } from "react-bootstrap"
import { Table, Button } from "react-bootstrap"

interface PublicationEntry {
  title: string
  year: string
  downloadLink: string
}

const PUBLICATIONS: PublicationEntry[] = [
  {
    year: "2022",
    title: "Exploring the Design Space of Social Physics Engines in Games",
    downloadLink: "/publications/SocialPhysicsEngines.pdf",
  },
  {
    year: "2022",
    title: "Neighborly: A Sandbox for Simulation-based Emergent Narrative",
    downloadLink: "/publications/Neighborly.pdf",
  },
  {
    year: "2021",
    title:
      "Centrifuge: A Visual Tool for Authoring Sifting Patterns for Character-Based Simulationist Story Worlds",
    downloadLink: "/publications/Johnson-Bey_Centrifuge_PLIE2021.pdf",
  },
  {
    year: "2020",
    title:
      "A novel approach towards computing global maps for multi-robotic operations in tactical environments",
    downloadLink: "/publications/DLC_SPIE_2020.pdf",
  },
  {
    year: "2018",
    title:
      "Head movements are correlated with other measures of visual attention at smaller spatial scales",
    downloadLink: "/publications/08362264.pdf",
  },
  {
    year: "2017",
    title:
      "Head movements during visual exploration of natural images in virtual reality",
    downloadLink: "/publications/HeadMovementsDuringVisualExploration.pdf",
  },
]

const CvPage = () => (
  <Layout>
    <Container>
      <h1>
        Shi Johnson-Bey CV{" "}
        <a href="/publications/Shi_Johnson-Bey_CV.pdf">
          <FontAwesomeIcon icon={"file-pdf"} color={"red"} />
        </a>
      </h1>
      <section className={`cv-section`}>
        <h3 className={`cv-section-title`}>Education</h3>
        <table>
          <tbody>
            <tr>
              <td>2020 - Present</td>
              <td>
                <div>
                  Doctor of Philosophy, Computational Media<br></br>
                  University of California - Santa Cruz<br></br>
                  Advisors: Michael Mateas, Noah Wardrip-Fruin
                </div>
              </td>
              <td>
                <StaticImage
                  alt=""
                  src="../images/ucsc.png"
                  style={{
                    maxHeight: "100%",
                    aspectRatio: "1",
                    maxWidth: "72px",
                    objectFit: "cover",
                  }}
                  className="mx-3"
                />
              </td>
            </tr>
            <tr>
              <td>2017 - 2019</td>
              <td>
                <div>
                  Master of Science, Biomedical Engineering<br></br>
                  Carnegie Mellon University<br></br>
                  Advisor: Pulkit Grover<br></br>
                </div>
              </td>
              <td>
                <StaticImage
                  alt=""
                  src="../images/cmu-600x600.png"
                  style={{
                    maxHeight: "100%",
                    aspectRatio: "1",
                    maxWidth: "72px",
                    objectFit: "cover",
                  }}
                  className="mx-3"
                />
              </td>
            </tr>
            <tr>
              <td>2013 - 2017</td>
              <td>
                <div>
                  Bachelor of Science, Computer Science<br></br>
                  Bachelor of Science, Neuroscience<br></br>
                  University of Delaware<br></br>
                </div>
              </td>
              <td>
                <StaticImage
                  alt=""
                  src="../images/youdee.png"
                  style={{
                    maxHeight: "100%",
                    maxWidth: "72px",
                  }}
                  className="mx-3"
                />
              </td>
            </tr>
          </tbody>
        </table>
      </section>

      <section className={`cv-section`}>
        <h3 className={`cv-section-title`}>Publications</h3>
        <Table striped bordered hover className="bg-white shadow">
          <thead>
            <tr>
              <th>Year</th>
              <th>Title</th>
              <th>Download</th>
            </tr>
          </thead>
          <tbody>
            {PUBLICATIONS.map((pub, index) => (
              <tr key={`pub_entry_${index}`}>
                <td>{pub.year}</td>
                <td>{pub.title}</td>
                <td>
                  <a
                    href={pub.downloadLink}
                    style={{ color: "white", textDecoration: "none" }}
                  >
                    <Button
                      variant="primary"
                      style={{ width: "100%", color: "white", margin: "auto" }}
                      className="m-auto"
                    >
                      <i className="fas fa-download" />
                      Download
                    </Button>
                  </a>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      </section>

      <section className={`cv-section`}>
        <h3 className={`cv-section-title`}>Talks</h3>
        <table>
          <tbody>
            <tr>
              <td>2021</td>
              <td>
                <a href="https://www.youtube.com/watch?v=B1OzIJAeN54">
                  AIIDE 21, PLIE Workshop, "Centrifuge: A Visual Tool for
                  Authoring Sifting Patterns for Character-Based Simulationist
                  Story Worlds"
                </a>
              </td>
            </tr>
            <tr>
              <td>2018</td>
              <td>
                <a href=" https://www.facebook.com/staysafeonline/videos/interview-with-dr-charles-johnson-bey-director-of-cyber-innovations-lockheed-mar/2094617107516164/">
                  National Cyber Security Alliance: "Interview with Dr. Charles
                  Johnson-Bey, Director of Cyber Innovations, Lockheed Martin
                  and Shi Johnson-Bey, Graduate Student, Carnegie Mellon
                  University"
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </section>

      <section className={`cv-section`}>
        <h3 className={`cv-section-title`}>Press</h3>
        <table>
          <tbody>
            <tr>
              <td>06/2019</td>
              <td>
                <div>
                  <a href="https://engineering.cmu.edu/news-events/news/2019/06/10-eeg-students.html">
                    Forney, E., "The power of EEG and student innovation".
                    Carnegie Mellon University, College of Engineering Magazine
                  </a>
                </div>
              </td>
            </tr>
            <tr>
              <td>10/2011</td>
              <td>
                <a href="https://www.baltimoresun.com/maryland/bs-md-cybersecurity-challenge-20111022-story.html">
                  Thomson, C., "'Cyber Challenge' encourages teen hackers to
                  seek security jobs". The Baltimore Sun
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </section>
      <section className={`cv-section`}>
        <h3 className={`cv-section-title`}>
          Committees and Leadership Positions
        </h3>
        <table>
          <tbody>
            <tr>
              <td>2022</td>
              <td>
                <div>
                  AIIDE-22 Experimental AI in Games (EXAG) Workshop Organizer
                </div>
              </td>
            </tr>
            <tr>
              <td>2022</td>
              <td>
                <div>
                  International Conference on Interactive Digital Storytelling
                  (ICIDS) Publicity Chair
                </div>
              </td>
            </tr>
            <tr>
              <td>2021</td>
              <td>
                <div>AIIDE-21 Artifact Evaluation Committee</div>
              </td>
            </tr>
            <tr>
              <td>2015 - 2016</td>
              <td>
                <div>UD Computer animation and Game Design Club President</div>
              </td>
            </tr>
            <tr>
              <td>2014 - 2016</td>
              <td>
                <div>University of Delaware Mascot Team Captain</div>
              </td>
            </tr>
            <tr>
              <td>2014 - 2017</td>
              <td>
                <div>UD RISE Program Student Advisory Council</div>
              </td>
            </tr>
          </tbody>
        </table>
      </section>
      <section className={`cv-section`}>
        <h3 className={`cv-section-title`}>Awards, Honors, and Fellowships</h3>
        <table>
          <tbody>
            <tr>
              <td>2018 - 2019</td>
              <td>
                <div>
                  ORAU Journeyman Fellow @ US Army Research Lab, Human Research
                  &amp; Engineering Directorate
                </div>
              </td>
            </tr>
            <tr>
              <td>2018</td>
              <td>
                <div>
                  Carnegie Mellon University Graduate Small Research Grant
                </div>
              </td>
            </tr>
            <tr>
              <td>2017</td>
              <td>
                <div>
                  ORAU Summer Journeyman Fellow @ US Army Research Lab, Human
                  Research &amp; Engineering Directorate
                </div>
              </td>
            </tr>
            <tr>
              <td>2017</td>
              <td>
                <div>
                  UD Computer Information Sciences Department, Steven Geracimos
                  Memorial Reward
                </div>
              </td>
            </tr>
            <tr>
              <td>2015 - 2016</td>
              <td>
                <div>Microsoft Student Partner @ University of Delaware</div>
              </td>
            </tr>
            <tr>
              <td>2015</td>
              <td>
                <div>
                  UD College of Engineering, Elbert C. Wisener RISE Corporate
                  Friends Award
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </section>
      <section className={`cv-section`}>
        <h3 className={`cv-section-title`}>Teaching Positions</h3>
        <table>
          <tbody>
            <tr>
              <td>Fall 2022</td>
              <td>
                <div>
                  Teaching Assistant, GAME 200: Game Design Systems, UC Santa
                  Cruz
                </div>
              </td>
            </tr>
            <tr>
              <td>Winter 2022, Spring 2022</td>
              <td>
                <div>
                  Teaching Assistant, CMPM 80K: Foundations of Video Game
                  Design, UC Santa Cruz
                </div>
              </td>
            </tr>
            <tr>
              <td>Fall 2021</td>
              <td>
                <div>Teaching Assistant, CMPM 176: Game Systems</div>
              </td>
            </tr>
            <tr>
              <td>Spring 2017</td>
              <td>
                <div>
                  Teaching Assistant, CISC 181: Intro to Computer Science II,
                  University of Delaware
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </section>
      <section className={`cv-section`}>
        <h3 className={`cv-section-title`}>Research Positions</h3>
        <table>
          <tbody>
            <tr>
              <td>06/2021 - 08/2021</td>
              <td>
                <div>
                  Part-time Research Staff @ American University, Computer
                  Science Dept.
                </div>
              </td>
            </tr>
            <tr>
              <td>08/2019 - 06/2020</td>
              <td>
                <div>
                  Research Software Engineer @ US Army Research Lab, Computer
                  Information Sciences Directorate, Adaptive Computing Lab
                </div>
              </td>
            </tr>
            <tr>
              <td>06/2015 - 08/2015</td>
              <td>
                <div>
                  Research Intern @ Johns Hopkins University, Mind Brain
                  Institute, Computational Neuroscience Lab
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </section>
      <section className={`cv-section`}>
        <h3 className={`cv-section-title`}>Software Engineering Positions</h3>
        <table>
          <tbody>
            <tr>
              <td>07/2021 - Present</td>
              <td>
                <div>
                  Co-Founder &amp; Lead Web Developer @ Jyackl LLC, Baltimore MD
                </div>
              </td>
            </tr>
            <tr>
              <td>06/2016 - 08/2016</td>
              <td>
                <div>Software Engineer Intern @ SevOne, Newark DE</div>
              </td>
            </tr>
          </tbody>
        </table>
      </section>
    </Container>
  </Layout>
)

export default CvPage
